<template>
	<view :class="{body:models=='1'||models=='', bodyNight:models=='0'}">
		<!-- 弹窗 -->
		<view class="model" v-if="showAudition">
			<view class="models">
				<img src="../../static/index/modelBg.png">
				<view class="closeModel" @click="closeAudition">×</view>
				<view class="contents">
					<view class="modelTitle">请输入邀请码</view>
					<input type="text" v-model="auditionCode"/>
					<view class="buttons">
						<button class="sure" @click="sendAuditionCode">确定</button>
						<button class="contact" open-type="contact">联系客服</button>
					</view>
				</view>
			</view>
		</view>
		<view class="list" v-for="(item,index) in data.data" :key="index" @click="toCurrentDetails(item)">
			<view class="content">
				<view class="title">
					{{item.title}}
					<view v-if="wrong==0">
						<view v-if="item.is_complete=='1'" class="finished">已完成</view>
						<view v-else class="unfinished">未完成</view>
					</view>
				</view>
				<view class="info">
					<view class="date">练习时间:{{item.time || '暂无'}}</view>
					<view class="date">总题数:{{item.total_question || item.total_score}}</view>
				</view>
			</view>
			<img src="../../static/index/right_icon.png" alt="">
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { currentIndex, politicsBrushWrong, politicsBrushCollection, question, invitation } from '@/common/http.api.js';
	export default{
		data() {
			return {
				showAudition:false, //弹窗显示状态
				auditionCode:'', //邀请码
				models:'1', //白天模式
				wrong:0,
				token:'',
				data:{},
				enter:'',
				current:'',
				pages:1,
				allPages:0
			}
		},
		// 滑动到页面底部加载下一页
		onReachBottom(){
			if(this.enter==0){
				// 真题
				// 判断是否有数据
				if(this.pages>=this.allPages){
					this.$u.toast("暂无更多")
				}else{
					this.pages++
					console.log(this.pages)
					currentIndex({token:this.token, page:this.pages}).then(res=>{
						console.log(res)
						this.data.data.push(...res.data.data.data)
					}).catch(err=>{
						console.log(err)
					})
				}
			}else{
				console.log("复习入口")
				// 复习
				if(this.current==0){
					//错题
					// 判断是否有数据
					if(this.pages>=this.allPages){
						this.$u.toast("暂无更多")
					}else{
						this.pages++
						console.log(this.pages)
						politicsBrushWrong({token:this.token, page:this.pages}).then(res=>{
							console.log(res)
							this.data.data.push(...res.data.data.data)
						}).catch(err=>{
							console.log(err)
						})
					}
				}else{
					//收藏
					// 判断是否有数据
					if(this.pages>=this.allPages){
						this.$u.toast("暂无更多")
					}else{
						this.pages++
						console.log(this.pages)
						politicsBrushCollection({token:this.token, page:this.pages}).then(res=>{
							console.log(res)
							this.data.data.push(...res.data.data.data)
						}).catch(err=>{
							console.log(err)
						})
					}
				}
			}
		},
		onLoad(option) {
			this.models = store.state.model
			this.token = store.state.vuex_token
			this.enter = option.enter
			this.current = option.current
			if(this.models=='0'){
				console.log('进入夜间模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#303030',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}else{
				console.log('进入白天模式')
				uni.setNavigationBarColor({
				    frontColor: '#ffffff',
				    backgroundColor: '#6ab4ff',
				    animation: {
				        duration: 0,
				        timingFunc: 'easeIn'
				    }
				})
			}
			if(option.enter==0){
				// 时政
				if(this.token){
					currentIndex({token:this.token}).then(res=>{
						console.log(res)
						this.data = res.data.data
						this.pages = res.data.data.current_page
						this.allPages = res.data.data.last_page
					}).catch(err=>{
						console.log(err)
					})
				}else{
					currentIndex().then(res=>{
						console.log(res)
						this.data = res.data.data
						this.pages = res.data.data.current_page
						this.allPages = res.data.data.last_page
					}).catch(err=>{
						console.log(err)
					})
				}
				
			}else{
				console.log("复习入口")
				// 复习
				if(option.current==0){
					//错题
					this.wrong = 1
					uni.setNavigationBarTitle({
						title: "时政刷题错题集"
					});
					politicsBrushWrong({token:this.token}).then(res=>{
						console.log(res)
						this.data = res.data.data
						this.pages = res.data.data.current_page
						this.allPages = res.data.data.last_page
					}).catch(err=>{
						console.log(err)
					})
				}else{
					//收藏
					this.wrong = 2
					uni.setNavigationBarTitle({
						title: "时政刷题收藏集"
					});
					politicsBrushCollection({token:this.token}).then(res=>{
						console.log(res)
						this.data = res.data.data
						this.pages = res.data.data.current_page
						this.allPages = res.data.data.last_page
					}).catch(err=>{
						console.log(err)
					})
				}
			}
			
		},
		onShow() {
			if(this.current==0){
				//错题
				politicsBrushWrong({token:this.token}).then(res=>{
					console.log(res)
					this.data = res.data.data
					this.pages = res.data.data.current_page
					this.allPages = res.data.data.last_page
				}).catch(err=>{
					console.log(err)
				})
			}
		},
		methods: {
			toCurrentDetails(e) {
				question({token:this.token, unit_id:e.id}).then(res=>{
					console.log(res)
					if(res.data.code == -1 && res.data.data == '没有权限'){
						this.showAudition = true
					}else{
						uni.navigateTo({
							url:'/pages/index/Details?unit_it='+e.id+'&wrong='+this.wrong+"&type="+2+"&title="+e.title+"&answerCard=1"
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			// 关闭弹窗
			closeAudition() {
				this.showAudition = false
			},
			// 发送邀请码
			sendAuditionCode() {
				invitation({token:this.token, audition_code:this.auditionCode}).then(res=>{
					console.log(res)
					if(res.data.code==-1){
						this.$u.toast(res.data.data)
					}else{
						this.$u.toast(res.data.data)
						this.showAudition = false
					}
				}).catch(err=>{
					console.log(err)
				})
			}
		}
	}
</script>

<style lang="scss">
	.model{
		position: fixed;
		left: 0;
		top: 0;
		width: 100vw;
		height: 100vh;
		background-color: rgba(0, 0, 0, 0.4);
		z-index: 5;
		.models{
			width: 90%;
			left: 50%;
			right: 50%;
			position: fixed;
			margin-top: 65%;
			// border-radius: 20rpx;
			// padding: 10rpx 0;
			// background-color: #fff;
			transform: translate(-50%,-50%);
			text-align: center;
			img{
				width: 101%;
				height: 200rpx;
			}
			.contents{
				border-bottom-left-radius: 20rpx;
				border-bottom-right-radius: 20rpx;
				padding: 10rpx 0;
				margin: 0 30rpx;
				margin-top: -50rpx;
				background-color: #fff;
			}
			.closeModel{
				top: -20rpx;
				right: -10rpx;
				width: 50rpx;
				height: 50rpx;
				line-height: 50rpx;
				color: #fff;
				border: 1px solid #fff;
				font-size: 38rpx;
				text-align: center;
				position: absolute;
				border-radius: 50%;
			}
			.modelTitle{
				font-weight: 600;
				margin: 30rpx 0;
			}
			input{
				border:1rpx solid #d1d1d1;
				width: 90%;
				margin: 0 5%;
				line-height: 88rpx;
				height: 88rpx;
				border-radius: 10rpx;
			}
			.buttons{
				display: flex;
				justify-content: space-between;
				padding: 50rpx 20rpx;
				button{
					color: #fff;
					border-radius: 10rpx;
					width: 45%;
					line-height: 80rpx;
				}
				.sure{
					background-color: #e73928;
				}
				.contact{
					background-color: #c0c0c0;
				}
			}
		}
	}
	
	.list{
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 20rpx;
		padding: 30rpx;
		background-color: #fff;
		border-radius: 10rpx;
		img{
			width: 26rpx;
			height: 26rpx;
		}
		.content{
			width: 90%;
			.title{
				font-size: 28rpx;
				line-height: 50rpx;
				.finished{
					background-color: #c3f6db;
					color: #22b267;
				}
				.unfinished{
					background-color: #ffded1;
					color: #c63904;
				}
				view{
					display: inline-block;
					font-size: 18rpx;
					border-radius: 6rpx;
					line-height: 28rpx;
					margin-left: 20rpx;
					padding: 2rpx 10rpx;
				}
			}
			.info{
				view{
					display: inline-block;
					font-size: 22rpx;
					margin-right: 20rpx;
				}
			}
		}
	}
	.body{
		padding: 40rpx;
		background-color: #f7f7fb;
		min-height: 100vh;
	}
	.bodyNight{
		padding: 40rpx;
		background-color: #3b3b3b;
		min-height: 100vh;
		.list{
			background-color: #303030;
			color: #fff;
			.content{
				.title{
					border-bottom: 4rpx solid #fff;
				}
			}
		}
	}
</style>